<script>
  import { onMount } from "svelte";
  import parallax from "./parallax";

  function startParallax() {
    let $ = document.querySelector.bind(document);

    let blob1 = $("#blob-1");
    let blob2 = $("#blob-2");
    let blob3 = $("#blob-3");
    let blob4 = $("#blob-4");
    let blob5 = $("#blob-5");
    let blob6 = $("#blob-6");

    const xmultiplier = window.innerWidth / 960;
    const overlapPixels = xmultiplier * 30;
    const initialOffset = window.pageYOffset;

    let staff = $("#staff").getBoundingClientRect();
    const staffYOffset = staff.top + initialOffset;
    const staffXOffset = staff.left + overlapPixels;

    let swArch = $("#softwareArchitecture").getBoundingClientRect();
    const swArchYOffset = swArch.top + initialOffset + 125;
    const swArchXOffset = swArch.right - overlapPixels;

    let customization = $("#customization").getBoundingClientRect();
    const customizationYOffset = customization.top + initialOffset + 250;
    const customizationXOffset = customization.left + overlapPixels;
    parallax(
      (lastScrollY, prefix, pos, setTickingFalse) => {
        let relativeY = lastScrollY / 2000;

        // big blue top corner
        prefix(
          blob1.style,
          "Transform",
          "translate3d(" +
            ((960 * xmultiplier).toFixed() - blob1.width).toFixed() +
            "px," +
            pos(-100, -200, relativeY, 0) +
            "px, 0)"
        );
        // big red
        prefix(
          blob2.style,
          "Transform",
          "translate3d(" +
            300 * xmultiplier +
            "px," +
            pos(504, -140, relativeY, 0) +
            "px, 0)"
        );

        // first small blue
        prefix(
          blob3.style,
          "Transform",
          "translate3d(" +
            448 * xmultiplier +
            "px," +
            pos(104, -3500, relativeY, 0) +
            "px, 0)"
        );

        // staff
        prefix(
          blob4.style,
          "Transform",
          "translate3d(" +
            (staffXOffset - blob4.getBoundingClientRect().width).toFixed() +
            "px," +
            pos(staffYOffset, -400, relativeY, 0) +
            "px, 0)"
        );

        // softwareArchitecture
        prefix(
          blob5.style,
          "Transform",
          "translate3d(" +
            swArchXOffset +
            "px," +
            pos(swArchYOffset, -400, relativeY, 0) +
            "px, 0)"
        );

        // customization
        prefix(
          blob6.style,
          "Transform",
          "translate3d(" +
            (
              customizationXOffset - blob6.getBoundingClientRect().width
            ).toFixed() +
            "px," +
            pos(customizationYOffset, -400, relativeY, 0) +
            "px, 0)"
        );

        setTickingFalse();
      },
      function() {
        if (window.innerWidth > 768) {
          blob1.classList.add("force-show");
        }

        blob2.classList.add("force-show");
        blob3.classList.add("force-show");
        blob4.classList.add("force-show");
        blob5.classList.add("force-show");
        blob6.classList.add("force-show");
      },
      startParallax
    );
  }
function displayConsoleMessage (){
  let g = "color:#00c389;font-weight:bold;";
  let b = "color:#001871;font-weight:bold;";
  let LagarsoftMainStyle="font-size:34px; font-weight:200; letter-spacing:0.02em; line-height:1.4em; font-family:helvetica,arial; color:rgba(0,0,0,0.9);";
 
