  %c██║     ██╔══██╗██╔════╝ ██╔══██╗██╔══██╗%c ██╔════╝██╔═══██╗██╔════╝╚══██╔══╝
  %c██║     ███████║██║  ███╗███████║██████╔╝%c ███████╗██║   ██║█████╗     ██║
  %c██║     ██╔══██║██║   ██║██╔══██║██╔══██╗%c ╚════██║██║   ██║██╔══╝     ██║
  %c███████╗██║  ██║╚██████╔╝██║  ██║██║  ██║%c ███████║╚██████╔╝██║        ██║
  %c╚══════╝╚═╝  ╚═╝ ╚═════╝ ╚═╝  ╚═╝╚═╝  ╚═╝%c ╚══════╝ ╚═════╝ ╚═╝        ╚═╝

  `;

 
    console.log(textOthers, g,b,g,b,g,b,g,b,g,b,g,b);	
    console.log("Hey we are looking for developers,");
